@import "../../index.scss";


.navbar{ 
    width: 100%;
    color: #fff;
    font-size: 14px;
    position: fixed;
    top: 0;
    z-index: 1000;
    background-color: linear-gradient(t0 top, transparent 0%, rgb(0,0,0,0.3) 50%);

    &.scrolled{
        background-color: var(--main-color);
    }
    
    .container { 
        padding: 0px 50px; 
        height: 70px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        
        .left {
            display: flex;
            align-items: center;

            img { height: 30px; margin-right: 40px;
            
                @include mobile{
                    display: none;
                }
            }

            span { margin-right: 20px; cursor: pointer;
            
                @include tablet{
                    display: none;
                }

                &.navbar-tablet--links{
                    @include tablet{
                        display: block;
                    }
                }
            }

        }


        .right {
            display: flex;
            align-items: center;

            .icon{ 
                margin: 0px 15px; 
                cursor: pointer;
            }

            img{ 
            width: 30px;
            height: 30px;
            border-radius: 5px;
            object-fit: cover;
            cursor: pointer;
            }


            .profile{
                .options{
                    display: none;
                    background-color: var(--main-color);
                    border-radius: 5px;;
                }

                span{
                    padding: 10px;
                    cursor: pointer;
                }
    

                &:hover{
                    .options{
                        display: flex;
                        flex-direction: column;
                        position: absolute;

                    }
                }
            }

        }
    }

}




